import { fabric } from "fabric";
import React, { useState, useEffect, useRef } from "react";
import backgroundImage from './img/background.png'
import styles from './Editor.module.css';
import Title from './Title';
import Layer from './Layer';
import Menubar from "./component/Menubar";
import * as common from "./component/submenu/common"


fabric.Object.prototype.getZIndex = function() {
    return this.canvas.getObjects().indexOf(this); //getObjects는 canvas에 있는 모든 객체를 모은다.
    //indexOf는 객체 중 fabric.Object의 index가 몇인지
}

export default function Editor(props) {//props로 회원 정보 보낸다.
    const [canvas, setCanvas] = useState(''); //useEffect하고 렌더링
    const[image,setImage] = useState(false); //이미지 불러왔을 때 전체 렌더링 
    const imageRef = useRef(false); //https://react.vlpt.us/basic/10-useRef.html 포커스를 옮기기 위함. 이미지를 불러오면 menubar에서 setImage()를 통해 렌더링, Editor.js에서 imageRef값이 변경 되면 submenu들을 렌더링 

    useEffect(() => {  //rendering 후 한 번 실행됨  
        setCanvas(initCanvas()); //기본값들로 canvas 세팅
    }, []);
    
    //Rightsidebar
    const [Items,setItems]=useState([]); //Items 배열 만듦
    const nextId=useRef(1);
    // var nextId =useRef(canvas.objectNum);

    //TODO: 실제로 객체도 지워지도록
    function delItem(canvas,id){
        setItems(//useState로 만든 Items 변경 함수
            Items=>(Items.filter(Item=>Item.id !== id))//Items 중에 주어진 id와 맞는 Items 요소 삭제
            )
        // console.log("objectid:",id);
        canvas.getObjects().forEach(function(object) {//getObjects는 canvas에 있는 모든 객체를 모은다. forEach는 배열 순회 함수
            if(object.id === id) {
                canvas.remove(object); //주어진 id와 일치하는 canvas 객체를 삭제
            }
        })
        canvas.discardActiveObject(); // 삭제 시 객체 선택 해제 
        common.updateStates(canvas); //submenu에 바뀐 상태 업데이트
        // canvas.renderAll();
    }
        
    function addLayerItem(canvas,imgSrc){
        //add items
        // console.log("addlayer");
        // let objectcomponent=ReactDOMServer.renderToStaticMarkup(object);
        // console.log("origianlItems: ",Items);
        let newItems=[
            {name:"items"+(canvas.objectNum),//nextId.current
            id:(canvas.objectNum),//nextId.current
            img:imgSrc},
        ...Items]; //기존 Items에 추가해준다.
        // console.log("obectNum(id): ",canvas.objectNum);
        // console.log("newItems: ",newItems);
        setItems(newItems); //바뀐 Items 업데이트
        // ++canvas.objectNum;//nextId.current+=1;
    }
    //index=>nexPos로 아이템이 보내진다.
    const moveItem =(contents,fromId, toId)=>{//Item, 요소 옮기기
        let oriPos= contents.findIndex(obj=>obj.id === Number(fromId)); //Number는 문자열을 숫자로 변환
        let newPos=contents.findIndex(obj=>obj.id === Number(toId)); //기존 id 위치에서 새로운 id 위치로 옮김
        const newContents=[...contents]; //contents 분해한 후 다시 배열로
        if(newPos<=0){ //새로운 위치가 0보다 작아진다면 0으로 옮기기
        newPos = 0;
        }
        console.log("oriPos",oriPos);
        console.log("newPos",newPos);
        //3=>0
        let moveStep=oriPos-newPos; //이만큼 옮겨야 함

        newContents.splice(oriPos,1); //oriPos index에서 1개 요소 제거
        newContents.splice(newPos,0,contents[oriPos]); //newPos index에 추가함
        setItems(newContents); //Items 업데이트

        //도형앞으로, 뒤로 미루는 부분
        let objects = canvas.getObjects(); //getObjects는 canvas에 있는 모든 객체를 모은다.
        let obj;
        console.log(typeof(fromId));
        console.log(typeof(objects[0].id));
        for (var i = 0; i < objects.length; i++) {//objects길이만큼 순회
            if (Number(objects[i].id) == fromId) {
                obj=objects[i];
                console.log(obj);
                if(moveStep>0){//위로 민다
                    for(i=0;i<moveStep;i++){
                        canvas.bringForward(obj);
                        console.log(obj);
                    }
                }else{//아래로 민다
                    for(i=0;i<(moveStep*-1);i++){
                        canvas.sendToBack(obj);
                    }
                }
                break
            }
        }
        // canvas.setActiveObject(obj);
        // canvas.renderAll(); 
    }

    useEffect(()=>{
        if (canvas) {
            
            fabric.Image.fromURL(backgroundImage,(img)=>{//URL로부터 이미지 가져옴. 여기서는 모눈종이 이미지
                img.default = true;
                canvas.setBackgroundImage(img,canvas.renderAll.bind(canvas),{//가져온 이미지를 배경이미지로 설정
                });//bind는 this 키워드가 canvas를 가리키도록 하기 위함
                canvas.renderAll(); //top canvas와 보조 컨테이너 캔버스 렌더링 
                common.updateStates(canvas); //canvas 상태 업데이트
            })

            canvas.componentSize = common.initialComponentSize(); // 최초 렌더링 이후 canvas에 componentSize 저장 
            common.setCanvasCenter(canvas);
            canvas.zoomInfo =1; //초기 캔버스 줌 x1
            
            window.onkeydown=(e)=>common.keyDownEvent(canvas,e) //키가 눌렸을 때 작동, delete, backspace누르면 객체 삭제

            var flag;
            window.addEventListener("resize", function (opt) { //브라우저 크기 resize에 따른 이벤트 
                common.setCanvasCenter(canvas)
                var innerWidth = common.getInnerSize(canvas)['innerWidth'];
                var innerHeight = common.getInnerSize(canvas)['innerHeight'];

                var currentWidth = common.getCanvasStyleWidth();
                var currentHeight = common.getCanvasStyleHeight();

                var ratio = canvas.width / canvas.height;

                if (innerWidth < currentWidth) {
                    flag = 'width';
                    common.setCanvasStyleSize(innerWidth, innerWidth * (1 / ratio))
                }
                else if (innerHeight < currentHeight) {
                    flag = 'height'
                    common.setCanvasStyleSize(innerHeight * ratio, innerHeight)
                } else {
                    if (currentHeight < canvas.height && flag === 'height')
                        common.setCanvasStyleSize(innerHeight * ratio, innerHeight)

                    if (currentWidth < canvas.width && flag === 'width')
                        common.setCanvasStyleSize(innerWidth, innerWidth * (1 / ratio))
                }

            });


            canvas.on({
                'mouse:wheel': (opt) => {//마우스휠에 따라 줌
                    var delta = opt.e.deltaY;
                    var value = delta<0? 0.1:-0.1;
                    canvas.zoomInfo += value;
                    common.zoom(canvas,1+value)
                    // document.getElementById('zoom-info').value =Math.fl oor(canvas.zoomInfo*100) + '%'
                }
            })

        }
    },[canvas])
   
    const initCanvas = () => {
        return (
        new fabric.Canvas('canvas', {
            width: 750,
            height: 500,
            initialWidth: 750,
            initialHeight:500, 
            objectNum : 0,
            undoStack :[],
            redoStack :[],
            componentSize:'',
        })
    )
    }
    

    return (
        <div className={styles.layout}>
            <div  className={styles.center}>
            <Title />
                <div className={styles.mainContainer}>
                    <canvas id="canvas"/>
                    <Layer canvas={canvas}></Layer>
                    <div id="layer"></div>
                </div>
                {canvas && <Menubar canvas={canvas} 
                    image={image} 
                    setImage={setImage} 
                    imageRef={imageRef}
                    addLayerItem={addLayerItem}
                    />}
            </div>
        </div>
    );
}